import request from './request.js'
import qs from 'qs'
//用户注册
export function userRegist(data) {
    return request({
        url: '/user/register',
        method: 'post',
        data
    })
}

//用户登录
export function userLogin(data) {
    return request({
        url: '/user/login',
        method: 'post',
        data
    })
}

//修改用户信息
export function updateUserPassword(data) {
    const { userId, password } = data;
    return request({
        url: `/user/${userId}`,
        method: 'put',
        data: {
            password
        }
    })
}