<template>
    <div id="login-regist-page">
         <div class="login-regist-container ">
             <!-- tabbar栏目 -->
            <div class="tab-bar">
                <div :class="ifActive(0)" @click="indexChange(0)">
                     用户登录
                </div>
                <span>|</span>
                <div :class="ifActive(1)" @click="indexChange(1)">
                     用户注册
                </div>
            </div>
            <!-- tabbar栏目结束 -->
            <div class="symbol">
                <img :src="require('../static/image/symbol.png')" id="logo" alt="">
            </div>
            <!-- 主要内容 -->
            <div class="main-container">
                 <div class="login-container" v-if="currentIndex==0" >
                    <el-form ref="loginForm" :model="loginForm" :rules="loginRules">
                        <el-form-item prop="userId">
                            <el-input v-model="loginForm.userId" 
                            placeholder="请输入用户账号"></el-input>
                        </el-form-item>
                        <el-form-item prop="password">
                            <el-input v-model="loginForm.password" 
                            placeholder="请输入用户密码" show-password></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button class='login-button' @click="login('loginForm')">立即登录</el-button>
                        </el-form-item>
                    </el-form>
                 </div>
                 <div class="regist-container" v-if="currentIndex==1" >
                    <el-form ref="registForm" :model="registForm" :rules="registRules">
                        <el-form-item prop="userId">
                            <el-input v-model="registForm.userId" 
                            placeholder="请输入用户账号"></el-input>
                        </el-form-item>
                        <el-form-item prop="password">
                            <el-input v-model="registForm.password" 
                            placeholder="请输入用户密码" show-password></el-input>
                        </el-form-item>
                        <el-form-item prop="confirm">
                            <el-input v-model="registForm.confirm" 
                            placeholder="请确认用户密码" show-password></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button class='regist-button' @click="regist('registForm')">立即注册</el-button>
                        </el-form-item>
                        <el-form-item>
                            <el-button class='reset-button' @click="resetForm('registForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                 </div>
            </div>
            <!-- 主要内容结束 -->
         </div>
    </div>
</template>

<script>
import {userRegist,userLogin} from '../network/loginAndRegist.js'
export default {
    async created(){
        // let res=await userRegist();
        // console.log(res);
    },
    data(){
       const passwordPattern = /^.*(?=.{6,16})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
       const userPattern=/^[a-zA-Z0-9_-]{4,16}$/;
       let validPass1=(rule,value,callback)=>{
           console.log('hello')
           let trueValue=value.trim();
           if(trueValue=='')
            callback(new Error("密码不能为空!"));
           else if(trueValue.length<6)
           callback(new Error("密码不能少于6位字符"));
           else if(trueValue.length>16)
           callback(new Error("密码不能多于16位字符"));
           else
           callback();
       }
       let validPass2=(rule,value,callback)=>{
           let trueValue=value.trim();
           if(trueValue=='')
           callback(new Error("请再次输入密码"));
           else if(trueValue!=this.registForm.password)
           callback(new Error('两次输入的密码不一致'));
           else 
           callback();
       }
       let validUser=(rule,value,callback)=>{
          let trueValue=value.trim();
           if(trueValue=='')
           callback(new Error("用户账号不能为空!"));
           else if(trueValue.length<4)
           callback(new Error("用户账号不能少于6位字符"));
           else if(trueValue.length>16)
           callback(new Error("用户账号不能多于16位字符"));
           else if(!userPattern.test(trueValue))
           callback(new Error("用户账号不可以包含特殊字符"))
           else
           callback();
       }
       return{
        
            currentIndex:0,
            //登录表单对象
            loginForm:{
                userId:'',
                password:'',
            },
            registForm:{
                userId:'',
                password:'',
                confirm:''
            },
            loginRules:{
                userId:[
                    {validator:validUser,trigger:'blur'}
                ],
                password:[
                    {validator:validPass1,trigger:'blur'}
                ]
            },
            registRules:{
                userId:[
                    {validator:validUser,trigger:'blur'}
                ],
                password:[
                    {validator:validPass1,trigger:'blur'}
                ],
                confirm:[
                    {validator:validPass2,trigger:'blur'}
                ]
            }
       }
    },
    methods:{
        //判断是否处于活跃状态
       ifActive(index)
       {
           return this.currentIndex==index?{"tab-item":true,"tab-item-active":true}:{"tab-item":true};
       },
       indexChange(index)
       {
           this.currentIndex=index;
       },
       //重置表单
       resetForm(formName)
       {
           this.$refs[formName].resetFields();
       },
       //登录提交表单
       async login(formName)
       {
          let loginForm=this.loginForm;
          this.$refs[formName].validate(async (valid)=>{
              if(valid)
              {
                  let res=await userLogin(loginForm);
                  console.log(res);
                  if(res.data.metaInfo.status==200&&res.data.data.status==1)
                   {
                    this.$message({
                    message: '恭喜你，登录成功！',
                    type: 'success'
                    });
                    this.currentIndex=0;
                    // let userInfo=JSON.stringify(res.data.data);
                    let userInfo=res.data.data;
                    userInfo.avatar=`http://8.135.61.132/cts/${userInfo.avatar}`;
                    userInfo=JSON.stringify(userInfo);
                    console.log('userInfo',userInfo);
                    localStorage.setItem('userInfo',userInfo);
                    this.$router.push('/index');
                  }
                  else
                  {
                     this.$message({
                    message: '登录失败，用户名或密码错误，或者您的账号已被封禁',
                    type: 'warning'
                    });
                  }
              }
          })
       },
       //注册提交表单
       async regist(formName)
       {
           let registForm=this.registForm;
           this.$refs[formName].validate(async (valid)=>{
               if(valid)
               {
                  let res=await userRegist(registForm);
                  console.log(res);
                  if(res.data.metaInfo.status==201)
                  {
                    this.$message({
                    message: '恭喜你，注册成功！现在将跳转到登录界面！',
                    type: 'success'
                    });
                    this.currentIndex=0;
                  }
                  else
                  {
                     this.$message({
                    message: '注册失败',
                    type: 'warning'
                    });
                  }
               }
               else
               return false;
           })
       }
    },
    computed:{
    }
}
</script>

<style scoped>
    #login-regist-page
    {
       height: 100%;
    }
    .login-regist-container
    {
        background-color: #fff;
        width: 500px;
        height: auto;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
    }
    .tab-bar
    {
        height: 80px;
        line-height: 80px;
        display: flex;
        border-bottom: 1px solid var(--font_color);
    }
    .tab-item
    {
        font-size:22px;
        color:var(--font_color);
        text-align: center;
        flex:1;
        
    }
    .tab-item:hover
    {
        cursor: pointer;
        color:var(--all_theme);
    }
    .tab-bar span
    {
        color:var(--font_color);
        
    }
    .tab-item-active
    {
        color:var(--all_theme);
        border-bottom: 1px solid var(--all_theme);
    }
    .login-button,.regist-button
    {
        background-color: var(--all_theme);
        color:#fff;
        width: 100%;
    }

    .login-container,.regist-container
    {
        padding:50px;
        padding-top:10px;
    }
     .symbol
     {
        text-align: center;
        padding-top:10px;
     }
    #logo
    {
        width: 60%;
    
    }
    .reset-button
    {
        width: 100%;
    }
</style>